.payment-waiting {
  margin-bottom: 24px;

  &__wrapper {
    padding: 28px;
    border: 1px solid $bg;
    border-radius: 5px;
    display: flex;
  }

  &__info-form {
    width: 41%;
    padding-right: 15px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__detail {
    &--sum {
      display: inline-block;
      margin-right: 24px;
    }

    &--time {
      display: inline-block;
      margin-bottom: 4px;
    }
  }

  &__new-link {
    margin-top: auto;
  }

  &__send-form {
    width: 59%;
  }

  &__send-form-wrapper {
    padding-left: 32px;
    border-left: 1px solid $stroke;
  }

  &__logo {
    text-align: right;
    margin-bottom: 17px;

    &::before {
      content: "";
      display: inline-block;
      width: 24px;
      height: 24px;
      background-image: url("../img/time.svg");
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  &__checkbox {
    margin-bottom: 14px;
  }
}
